<!-- 列表页 -->
<template>
  <div class="app">
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh" style="width:100%">
      <div class="tabs">
          <span v-for="(item,index) in tabs" :key="index" :class="[tabindex==index?'action':'tabitem']" class="taitem" @click="onClick(item)">{{item}}</span>
      </div>
      <van-list
        v-model="loading"
        :finished="finished"
        class="van-list"
        :immediate-check="immediate"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <div class="wrap">
          <div
            class="listwrap"
            v-for="(item,index) in list"
            :key="index"
            @click="toDet(item.uid)"
          >
           <span class="scoreNum">{{item.score}}</span>
            <div class="left">
              <img src="https://head.dziqian.com/avtaurl.png" alt />
            </div>
            <div class="right">
              <div>{{item.name}}</div>
              <div>
                <van-rate v-model="item.score " allow-half readonly />
              </div>
            </div>
          </div>
        </div>
      </van-list>
    </van-pull-refresh>
  </div>
</template>
<script>
import axios from "./../axios/axios";
export default {
  data() {
    return {
      tabs: ["最高分", "最差分", "最近评分"],
      loading: false,
      finished: false,
      list: [],
      check: true,
      page: 0,
      immediate: false,
      tabindex: 0,
      type: "best",
      isLoading: false,
      Refresh: false
    };
  },
  components: {},
  computed: {},
  mounted() {
    this.onLoad();
  },
  methods: {
    // 列表数据
    onLoad() {
       this.page++;
      let param = { page: this.page, tag: this.type };
      axios
        .post("reviewlist", param)
        .then(res => {
           
          this.loading = false;
          let listdata = res.data.data.list;
          if (res.data.code && listdata) {
            this.page == 1
              ? (this.list = listdata)
              : (this.list = [...this.list, ...listdata]);
            listdata.length == 0
              ? (this.finished = true)
              : (this.finished = false);
          } else {
            this.$toast.fail(res.data.data.msg ? res.data.data.msg : "参数有误");
            if (res.data.data== 'timeout'){
                   localStorage.removeItem('token')
                   this.$router.push({name:'login'})
            }
          }
          this.$toast.clear();
          if (this.Refresh) {
            this.$toast.success({
              message:"刷新成功",
              duration: 1000, 
            });
            this.Refresh = false;
          }
          this.isLoading = false;
         
        })
        .catch(err => {
          console.log(err);
          this.$toast.fail(err.msg ? err.msg : "reviewlist参数有误");
        });
    },
    // 详情页
    toDet(uid){
      if (uid){
this.$router.push({ name: 'Detailed', params: { uid:uid }})
      }else{
          this.$toast.fail("uid参数有误");
      }
            
    },
    // tab点击
    onClick(title) {
      let _this = this;
      this.$toast.loading({
        mask: true,
        message: "加载中..."
      });
      
      switch (title) {
        case "最高分":
          _this.type = "best";
          _this.tabindex = 0
          break;
        case "最差分":
          _this.type = "bad";
           _this.tabindex = 1
          break;
        case "最近评分":
          _this.type = "new";
           _this.tabindex = 2
          break;
        default:
          _this.type = "best";
           _this.tabindex = 0;
           break;
      }
      this.page = 0;
      console.log(_this.type);
      this.finished = false;
      this.onLoad();
    },
    onRefresh() {
      this.Refresh = true;
      this.page = 0;
      this.onLoad();
    }
  }
};
</script>
<style  scoped>
.app {
  width: 100%;
  height: 100%;
}
.tabs {
  max-width: 768px;
  width: 100%;
  height: 50px;
  line-height: 50px;
  position: fixed;
  z-index: 999;
  padding: 4px 0;
 
  background: white;
  color: black;
  display: flex;
  
  align-items: center;
  box-sizing: border-box;
  border-bottom: 1px solid yellowgreen;
  border-top: 1px solid yellowgreen;
}
.tabitem,.action:last-child{
  border-left: 1px solid yellowgreen
}

.tabitem{
  flex: 1;
  text-align: center;
  border-right:solid 1px yellowgreen; 
  /* border-radius: 2px; */
}
div.tabs .van-tabs__wrap{
  height: 100% !important;
}
.listwrap {
  display: flex;
  width: 95%;
  margin: 10px auto;
  padding: 8px 0;
  justify-content: space-between;
  position: relative;
  align-items: center;
  background: white;
  border-radius: 5px;
  position: relative;

}
.scoreNum{
  position: absolute;
  top: 30px;
  right: 30px;
  color: red;
  font-weight: 600;
  font-size: 16px;
}
.action{
  background: yellowgreen;
  color: white;
  flex: 1;
  text-align: center;
   border-right:solid 1px yellowgreen; 
}
.tabitem,.action:last-child{
  border-right: 0
}
.taitem:last-child{
  border-right:solid 1px yellowgreen; 
}

.left {
  width: 50px;
  margin-left: 10px
}
.left img {
  width: 100%;
}
.right {
  width: calc(100vw - 90px);
  font-size: 18px;
}
.right > div {
  margin: 4px 0;
}
.van-list {
  /* width: 95%; */
  height: 100%;
  margin: 0 auto;
}
.wrap {
  padding-top: 50px;
   background: #e9e9e9;
}
.van-tabs__nav--card{
  margin: 0 !important
}
.van-list__finished-text {
  background: white;
}

</style>


